.mainInformation {
    width: 100%;
    margin: auto;
    /* box-shadow: 0 2px 4px 2px rgb(0 0 0 / 20%); */
    box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
    padding: 1rem;
    margin-bottom: 1rem;
    border-radius: 8px;
}

.mainInformation button {
    margin-top: 0;
    margin-bottom: 0;
}

.innerInformation {
    width: 100%;
}

.informationTitle ul {
    font-family: 'Segoe UI';
    margin-bottom: 0;
    font-size: 18px;
    text-align: center;
    text-transform: capitalize;
    color: #707070;
    margin-left: 1rem;
}

.information_parent p {
    font-family: 'Segoe UI Bold';
    font-size: 14px;
    text-transform: capitalize;
    color: #5c6dff;
}

.editableBtn {
    width: 100%;
    display: flex;
    margin-bottom: 2%;
    border-radius: 5px;
    background: #eef0ff;
    box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
}

.editableBtn button {
    padding: 0.5rem 1rem;
    outline: none;
    border: none;
    font-family: 'Segoe UI';
    color: #000;
    background-color: #eaf2ff;
    border-radius: 0px;
}

.editableBtn button:nth-child(2) {
    background-color: transparent;
    border: 1px solid #5cb85c;
    color: #5cb85c;
    transition: 0.3s ease-in-out;
}

.editableBtn button:nth-child(2):hover {
    background-color: #5cb85c;
    color: #fff;
}

.editableBtn button i {
    margin-right: 10px;
}

.informationForm {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.informationInputForm {
    width: 100%;
    background-color: #fff;
    position: relative;
    border-radius: 15px;
}

