.informationImageForm {
    width: 40%;
}

.informationInputForm div {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid #ebebeb;
    margin-bottom: 10px;
    border-radius: 5px;
    box-shadow: 0px 0px 1px rgb(0 0 0 / 18%);
}

.informationInputForm div p {
    margin-top: 16px;
    font-size: 1.1rem;
    color: #999;
}

.informationInputForm div input {
    width: 50%;
    height: 40px;
    font-family: 'Segoe UI';
    padding-left: 10px;
    font-size: 1.1rem;
    outline: none;
    border: none;
    background-color: transparent;
    color: #707070;
}

.informationImageForm {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.informationImageForm div {
    width: 70%;
    box-shadow: 0 2px 4px 2px rgba(0, 0, 0, 0.2);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom: 5%;
    min-height: 26vh;
    border-radius: 15px;
    background-color: #fff;
    z-index: 1;
    position: relative;
    overflow: hidden;
}

.informationImageForm div p {
    font-size: 2rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: bold;
    color: #000;
}

.informationImageForm div h2 {
    color: #000;
}

.informationImageForm div img {
    position: absolute;
    width: 20%;
    bottom: 10px;
    right: 10px;
}

.brochureBorder {
    position: absolute;
    width: 8px;
    height: 93%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    background-color: #02044a;
    border-radius: 999px;
}

