.information_parent {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 12%;
    margin-left: 10px;
    padding: 8px 0px;
}

.information_save_parent {
    display: flex;
    justify-content: flex-end;
    /* width: 100%; */
}

.information_save_parent:hover {
    cursor: pointer;
}

.information_save {
    background: linear-gradient(262deg, #45a4e4 0%, #5c6dff 100%);
    color: #fff;
    outline: none;
    border: none;
    padding: 0.5rem 2rem;
    border-radius: 15px;
    font-size: 0.8rem;
    font-family: 'Segoe UI Semibold';
}

.information_cancel {
    background: #707070;
    color: #fff;
    outline: none;
    border: none;
    padding: 0.5rem 2rem;
    border-radius: 15px;
    font-size: 0.8rem;
    font-family: 'Segoe UI Semibold';
}

.Edit-icon_information {
    display: flex;
    align-items: center;
    font-size: 18px !important;
    color: blue !important;
}

.Edit-icon_information:hover {
    cursor: pointer;
}

.handleButtons {
    display: flex;
    justify-content: space-between;
}
