.mainRules {
    width: 100%;
    margin: auto;
    padding: 1rem;
    margin-bottom: 1rem;
    box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
    border-radius: 20px;
    background-color: #ffffff;
}

.rules_parent {
    justify-content: center;
    display: flex;
    align-items: center;
    width: 13%;
    padding: 8px 0px;
    margin-left: 6px;
}

.rules_parent p {
    font-family: 'Segoe UI Bold';
    font-size: 14px;
    text-transform: capitalize;
    color: #5c6dff;
}

.rulesCard {
    width: 100%;
    border-radius: 20px;
}

.rulesUpper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0 1rem;
}

.rulesUpper h4 {
    font-family: 'Segoe UI';
    font-size: 1rem;
    color: #5c6dff;
    margin-top: 1rem;
}

.rulesUpper p {
    font-family: 'Segoe UI Semibold';
    font-size: 1rem;
    color: #5c6dff;
    margin-top: 1rem;
}

.openTiming i,
.weekendOpening i {
    margin-right: 4px;
}

.questionPart {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 0 1rem;
    padding-right: 5rem;
}

.date_parent {
    display: flex;
    justify-content: space-between;
    width: 100%;
    gap: 1rem;
    align-items: center;
}

.leftQuestion {
    width: 70%;
}

.rulesMark {
    width: 40%;
    padding-left: 40px;
    margin-top: -4px;
}

.rulesMark i {
    margin-right: 10%;
    font-size: 1.3rem;
}

.leftQuestion p {
    font-family: 'Segoe UI';
    font-size: 1.1rem;
    color: #666;
}

.leftQuestion p::before {
    content: '';
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #666;
    display: inline-block;
    margin-right: 10px;
}

.submitEditBtn {
    display: flex;
    justify-content: flex-end;
    margin-top: 1rem;
}

.conditionalPadding {
    padding: 10px;
}

.editableBtn_rules {
    width: 100%;
    display: flex;
    border-radius: 23px;
    background: #eef0ff;
    margin-bottom: 1%;
    box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
}

.rulesMark img {
    width: 12px;
    transform: translateY(5px);
}

.cancel-img {
    filter: invert(16%) sepia(200%) saturate(6753%) hue-rotate(2deg)
        brightness(101%) contrast(122%);
}

.check-img {
    filter: invert(42%) sepia(19%) saturate(6449%) hue-rotate(219deg)
        brightness(105%) contrast(451%);
}

.submitEditBtn button:hover {
    background-color: #5cb85c;
    color: #fff;
}

.information_cancel:hover,
.information_save:hover {
    cursor: pointer;
}
