.container-body > .content-body {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-top: 4.2rem;
    overflow: hidden;
}

.container-body {
    height: 100vh;
    display: flex;
    margin-left: 11%;
    background-image: linear-gradient(to right, #015f9a, hsl(202, 97%, 37%));
    width: 89%;
    position: relative;
}

.innerMainVerify.innerMainVerify_clientDashboard {
    position: absolute;
    top: 0;
    width: 75%;
    height: 10%;
    z-index: 100;
}

.user-project .user-project-details {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 10%;
    overflow: auto;
}

.user-project-details > .graphic {
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.graphic-illustration-heading h6 {
    font-family: 'Segoe UI Semibold';
    font-size: 15px;
    text-transform: capitalize;
    /* color: #FFFFFF; */
}

.not_found.clientNewestDashboard {
    width: max-content;
    height: 367px;
    margin: auto;
    margin-top: 2rem;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    display: flex;
}

.not_found.clientNewestDashboard img {
    width: 23rem;
}

.container-body > .content-body > .content-leftBody {
    min-width: 75%;
    /* position: relative; */
}

@media (max-width: 768px) {
    .project-details-card {
        width: 300px;
    }
}

.showDetail_onClientNewestDashboard {
    cursor: pointer;
    right: 63px;
    padding: 4px 9px;
    text-align: center;
    font-family: 'Segoe UI Semibold';
    font-size: 12px;
    padding-right: 13px;
    color: #45a4ea;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ffffff;
    border-radius: 1rem;
    box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
}

.showDetail_onClientNewestDashboard:hover {
    color: blue;
}

.graphic {
    display: flex;
    justify-content: space-between;
    width: 88%;
    margin: auto;
    margin-top: 1.3rem;
    margin-bottom: 0.3rem;
}

.no_project_found {
    font-family: 'Segoe UI Semibold';
    font-size: 1rem;
    margin-top: 2rem;
    color: #ffffff;
}

.detailsCard-header {
    word-break: break-all;
}

@media (max-width: 768px) {
    .container-body > .content-body > .content-leftBody {
        width: 100%;
        height: 87%;
    }

    .container-body {
        margin-left: 0;
        width: 100%;
    }
}

@media (max-width: 360px) {
    .container-body > .content-body {
        overflow: visible;
    }
}
