.agencyNewestDashboard > div {
    width: 88%;
}

.user-project_parent {
    overflow-y: auto;
    height: 69vh;
    padding-bottom: 6rem;
    display: flex;
    flex-wrap: wrap;
}

.conditional_flex {
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 0;
}

.sidebar_parent .container-sPidebar {
    position: fixed;
    top: 0;
}

.graphic.graphic_agencyDashboard {
    margin: 2rem auto;
}

.verify_update_wait {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 0rem;
    /* border: 1px solid; */
    margin: auto;
    width: 75%;
    height: 10%;
    justify-content: space-evenly;
    z-index: 2;
}

/* .interested_or_not.verify_or_not div {
  width: 39%;
  font-size: 12px;
} */

.not_found.agencyNewestDashboard {
    /* width: max-content;
  height: 325px; */
    /* margin: 2rem auto 0rem; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.not_found p {
    color: black;
}

.not_found.agencyNewestDashboard p {
    font-family: 'Segoe UI Semibold';
    font-size: 1.2rem;
    margin-top: 1rem;
    text-align: center;
}

.not_found.agencyNewestDashboard img {
    width: 16rem;
}

.conditional_marginTop {
    margin-top: 20px;
}

.navbar_parent .navbar {
    width: 90%;
    margin-left: 10%;
}

@media (max-width: 768px) {
    .project-details-card {
        width: 300px;
    }
}

.down_seperator {
    /* position: absolute; */
    width: 40%;
    background-color: #d5d5d5;
    height: 1px;
    color: green;
    align-self: center;
}

/* .mainUpdateVerify {
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  height: 40px;
} */

.innerMainVerify {
    width: 100%;
    /* color: #fff; */
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: #68e1fd; */
    height: 100%;
    border-radius: 0.3rem;
}

.innerMainVerify img {
    width: 20px;
}

.innerMainVerify div {
    margin-right: 5px;
    width: 2%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.innerMainVerify p {
    font-family: 'Segoe UI';
    font-size: 0.8rem;
    margin-top: 0;
}

.innerMainVerify span {
    color: #5c1fff;
    /* color: black; */
    font-family: 'Segoe UI Semibold';
    font-size: 0.9rem;
    margin: 0 10px;
    /* border-bottom: 1px solid #5c1fff; */
    cursor: pointer;
}

.conditional_opacity {
    filter: opacity(0.5);
}

.modal-custom {
    position: fixed;
    height: 100%;
    width: 74%;
    top: 4rem;
    right: 0;
    z-index: 1000;
    /* background: rgba(0, 0, 0, 0.9); */
    background: rgba(0, 0, 0, 0.2);
}

.userprofile-circle.nav-left-item img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
}

.requiredStar {
    color: red;
}

.interested_or_not.verify_or_not {
    width: 90%;
    margin-top: 1rem;
}

.update_now {
    background-color: #45a4ea !important;
    color: white !important;
}

.update_now:hover {
    color: white !important;
}

.update_later {
    background-color: #eaf2ff !important;
}

.verify_or_not div:hover {
    cursor: pointer;
}

@media (max-width: 768px) {
    .not_found.agencyNewestDashboard img {
        width: 14rem;
    }
}

@media (max-width: 360px) {
    .user-operations {
        flex-direction: column;
        justify-content: space-between;
        height: 30rem;
    }

    .innerMainVerify p {
        font-size: 0.8rem;
    }

    .innerMainVerify span {
        font-size: 0.9rem;
    }

    .conditional_marginTop {
        margin-top: 10px;
    }
}
