.mainDeveloperList {
  width: 100%;
  height: 100%;
}

.innerDeveloperList {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.developerCard {
  /* width: 30%; */
  width: 33%;
  background-color: #fff;
  padding: 1rem;
  border-radius: 15px;
  position: relative;
  margin: 2rem 3rem;
  box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
}

.cross-icon {
  position: absolute;
  right: 17px;
  top: 16px;
}

.cross-icon:hover {
  cursor: pointer;
}

.cross-icon .fas {
  font-size: 1.5rem !important;
}

.cross-icon:hover {
  cursor: pointer;
}

.developerCardBorder {
  position: absolute;
  width: 8px;
  height: 95%;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  background-color: #02044a;
  border-radius: 999px;
}

.developerNameExp {
  display: flex;
  flex-direction: column;
  margin-bottom: 3%;
  padding-bottom: 5%;
}

.developerName h2 {
  margin-bottom: 0;
}

.rounded_developerList {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #26E826;
  margin-left: 1rem;
}

.developerBudgetResume {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
}

.developerName {
  width: 90%;
  position: relative;
  display: flex;
  align-items: center;
}

.developerName h2 {
  font-family: 'Segoe UI Bold';
  font-size: 1.4rem;
  color: #45a4ea;
}

.developerName p {
  font-family: 'Segoe UI';
  color: #999;
  font-size: 0.9rem;
}

.developerExp p {
  font-family: 'Segoe UI Semibold';
  color: green;
  font-size: 0.9rem;
}

.developerExp h6 {
  color: #000;
  font-weight: bold;
  font-size: 1.1rem;
  white-space: nowrap;
}

.developerTech h6 {
  font-family: 'Segoe UI Semibold';
  color: #5C6DFF;
  margin-bottom: 3.2%;
  font-size: 16px;
}

.developerTechNames {
  display: flex;
  grid-row-gap: 8px;
  justify-content: flex-start;
  align-items: center;
  flex-wrap: wrap;
}

.developerTechNames p {
  font-family: 'Segoe UI';
  color: #707070;
  padding: 0.3rem 0.8rem;
  border: 1px solid #707070;
  border-radius: 999px;
  margin-bottom: 3px;
  margin-right: 15px;
  font-size: 0.7rem;
}

.developerBudget {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.developerBudget p {
  font-family: 'Segoe UI Semibold';
  font-size: 16px;
  text-transform: capitalize;
  color: #5C6DFF;
}

.developerBudget h6 {
  font-family: 'Segoe UI Semibold';
  font-size: 14px;
  text-transform: capitalize;
  color: #707070;
  margin-top: 10px;
  text-align: center;
}

.developerResume button {
  padding: 0.4rem 1rem;
  border-radius: 20px;
  background: linear-gradient(262deg, #45A4E4 0%, #5C6DFF 100%) !important;
  font-family: 'Segoe UI';
  color: white;
  font-size: 0.8rem;
  outline: none;
  border: none;
  box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
}

.developerResume button i {
  margin-right: 5px;
}

.addMoreDeveloper {
  text-align: center;
  padding: 5px;
  padding-top: 10px;
  background-color: #02044a;
  color: #fff;
  margin: auto;
}

.addDeveloperText {
  font-family: 'Segoe UI';
  font-size: 1.1rem;
  margin-top: 1rem;
}

.developer-detail {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 1.5rem 0;
}

.quotation .nav {
  justify-content: space-evenly;
}

.add-developer_parent {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 255;
  cursor: pointer;
}

.reject_or_not {
  margin: auto;
  width: 48%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  justify-content: space-around;
  /* margin: auto; */
}

.reject_or_not-label {
  text-align: center;
  font-weight: 'Segoe UI';
  font-size: 20px;
  font-style: normal;
}

.reject_or_not>div {
  /* color: white;
  background-color: #3498db;
  padding: 8px;
  text-align: center;
  border-radius: 11px;
  width: 33%; */

  color: white;
  background-color: #3498db;
  padding: 4px;
  text-align: center;
  border-radius: 11px;
  width: 23%;
}

.QuotationModal.QuotationModal_DeveloperList {
  max-width: 490px;
}

.QuotationModal_DeveloperList button {
  top: 0px !important;
  right: 10px !important;
}

.reject_or_not>div:hover {
  cursor: pointer;
}

.developers-status_parent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  height: 44px;
}

.developers_content {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.developers-status_parent:hover {
  cursor: pointer;
}

.developer-status_developerListChild {
  display: flex;
  justify-content: space-between;
  width: 100%;
  font-size: 14px !important;
}

.developer-status_developerList p {
  font-family: 'Segoe UI Semibold';
  font-size: 16px;
  text-transform: capitalize;
  color: #5C6DFF;
}

.statusLabel {
  font-weight: 500;
  font-size: 10px;
  line-height: 15px;
  text-align: center;
  text-transform: capitalize;
  color: #838383;
  font-family: 'Open Sans', sans-serif;
}

.availabilityToggle {
  margin-top: 0.5rem;
}

.conditionalRotate {
  transform: rotate(180deg);
}

.conditionalColor {
  background-color: #FF0000;
}

@media (max-width: 1200px) {
  .developer-detail {
    flex-direction: column;
    align-items: flex-start;
    height: 9rem;
    margin: 1rem 0;
  }

  .developerBudget h6 {
    text-align: initial;
    margin-top: 0;
  }
}