.commentBox_parent {
    display: flex;
    flex-wrap: wrap;
}

.is_flex_direction {
    flex-direction: row !important;
    width: 100% !important;
}

.normal_acceptOrReject_clientCommentBox {
    font-size: 25px;
    font-weight: 600;
    line-height: 39px;
    text-align: center;
    letter-spacing: 0.4px;
    color: #3a3a3a;
}

.normal_acceptOrReject_clientCommentBox p {
    padding: none;
    font-size: 1rem;
    transform: translateY(-13px);
}

.sendIcon_clientCommentBox:hover {
    cursor: pointer;
    background-color: #31c53180;
}

.sendIcon_clientCommentBox.MuiSvgIcon-root {
    height: 40px;
    width: 40px;
    background: #45a4ea;
    border-radius: 50%;
    padding: 9px;
    color: white !important;
}

.commentParent .MuiInputBase-input {
    font-size: 10px;
    /* font-family: 'Open Sans', sans-serif; */
    font-family: 'Segoe UI Semibold';
    /* font-style: normal;
    font-weight: 600; */
    line-height: 17px;
    text-transform: uppercase;
}

.datePicker_quotation:hover {
    cursor: pointer;
}

.commentBox > p {
    font-size: 10px;
    color: #484848;
    font-family: 'Segoe UI';
}

.postQuotation .makeStyles-margin-1 {
    width: 100%;
}

.price-section .makeStyles-margin-8 {
    margin-left: 0 !important;
}

.detailed_description_clientCommentBox {
    display: flex;
    margin-left: 8rem;
    flex-direction: column;
}

.radioButtons_with_textBox .MuiSvgIcon-root {
    font-size: 13px;
}

.reject-reason_label {
    text-align: center;
    /* font-family: 'Open Sans', sans-serif; */
    font-family: 'Segoe UI Semibold';
    /* font-weight: 600; */
    font-size: 20px;
    font-style: normal;
}

.submit-rejection,
.quotationSubmit_clientCommentBox {
    width: 99%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem;
}

.submit-rejection > div,
.quotationSubmit_clientCommentBox > button {
    display: flex;
    align-items: center;
    outline: none;
    border: none;
    background-image: linear-gradient(
        284deg,
        rgb(3, 118, 186) 0%,
        rgb(1, 48, 77) 100%
    );
    border-radius: 25px;
    color: white;
    background-color: #3498db;
    padding: 0.3rem 1.5em;
    box-shadow: 1px 2px 3px rgb(0 0 0 / 18%);
    font-size: 0.9rem;
}

.acceptance-parent_clientCommentBox > h2::after {
    margin-left: 90px;
}

.reject-reason_label h2 {
    font-weight: bold !important;
    border-bottom: none !important;
    padding-bottom: 0% !important;
    margin-bottom: 5% !important;
}

.radioButtons_with_textBox {
    margin-top: 1rem;
    margin-left: 5.5rem;
}

.rejection_modal_clientCommentBox .MuiTypography-body1 {
    font-family: 'Segoe UI' !important;
    font-size: 14px;
    line-height: 17px !important;
    text-transform: capitalize !important;
}

.rejection_modal_clientCommentBox .MuiFormControlLabel-labelPlacementStart {
    margin-left: 70px !important;
}

.rejection_modal_clientCommentBox .MuiRadio-root {
    margin-left: 110px !important;
}

.submit-rejection:hover {
    cursor: pointer;
}

.conditional_datePicker {
    background-color: #d9d6d62e;
}

.acceptance-parent_clientCommentBox {
    max-width: 530px;
}

.QuotationModal.QuotationModal_clientCommentBox {
    max-width: 530px;
    width: 100%;
    border-radius: 10px;
}

.tableContentQuotation p {
    margin-top: 3px;
    margin-right: 20px;
    font-family: 'Segoe UI';
    font-size: 1rem;
}

.tableContentQuotation.finalCost > input {
    height: 40px;
    padding-left: 10px;
    outline: none;
    border: 1px solid #e0e0e0;
    font-size: 0.9rem;
    border-radius: 5px;
    padding-right: 2rem;
}

.datePickers {
    border: 1px solid #e0e0e0;
    border-radius: 5px;
    height: 42px;
    display: flex;
    align-items: center;
    padding: 0 5px;
    padding-right: 1rem;
}

.datePickers .MuiFormControl-root .MuiButtonBase-root {
    padding: 0 !important;
    /* padding-right: 1rem !important; */
}

.datePickers .MuiFormControl-root .MuiButtonBase-root:focus {
    outline: none !important;
}

.datePickers .MuiInputBase-root:focus {
    border: none !important;
    outline: none !important;
}

.waiting_left {
    text-align: right;
    /* margin-top: -13px; */
    font-size: 10px;
    color: #484848;
    font-family: 'Segoe UI';
}

.reject_textArea {
    padding: 10px;
    margin: 1rem 0rem;
    width: 70%;
    font-size: 14px;
    max-height: 6rem;
    border-radius: 4px;
}
